@import url(https://fonts.googleapis.com/css2?family=Noto+Serif+JP&family=Roboto&display=swap);
* {
  margin: 0;
  padding: 0;
  font-family: 'Raleway', sans-serif;
  font-size: 10px;
}

* {
  font-family: 'Roboto', sans-serif;
}

.App {
  overflow-x: hidden;
}

.particle__bg {
  position: absolute;
  z-index: -2;
}

section.desc {
  margin: 10% 0 0 5%;
  height: 20px;
}

.desc__title span {
  color: #e6e500;
  font-size: 5rem;
}

.jp_font {
  font-family: 'Noto Serif JP', serif;
}

section.subtitle {
  margin-top: 5%;
  margin-left: 5%;
}

.desc__subtitle span {
  font-size: 3rem;
  color: #5fc1c7;
}

@media (max-width: 1080px) {
  section.desc {
    margin: 1% 0 0 5%;
    height: 40px;
  }

  .desc__subtitle {
    margin-top: 32%;
  }
}

@media (max-width: 1080px) and (orientation: landscape) {
  section.desc {
    margin: 1% 0 0 5%;
  }

  .desc__subtitle {
    margin-top: 7%;
  }
}

section.button {
  position: absolute;
  margin: 3% 0 0 5%;
}

.button__content {
  display: flex;
  width: 60rem;
  flex-wrap: wrap;
  align-items: center;
}

@media (max-width: 1000px) {
  section.button {
    margin: 5% 0 0 5%;
  }

  .button__content {
    display: flex;
    width: 30rem;
    flex-wrap: wrap;
    align-items: center;
  }
}

@media (max-width: 1080px) and (orientation: landscape) {
  section.button {
    margin: 2% 0 0 5%;
  }

  .button__content {
    display: flex;
    width: 60rem;
    flex-wrap: wrap;
    align-items: center;
  }
}

.button__content a {
  text-decoration: none;
  padding: 2rem 2rem;
  font-size: 4rem;
  width: 27rem;
  margin: 0 2rem 2rem 0;
  box-sizing: border-box;
  border-radius: 0.5rem;
  cursor: pointer;
  border: solid 1px #e5e600;
}

.button__content a:hover {
  transform: translateY(-5px);
}

.button__content a p {
  font-size: 1.5rem;
  color: #e5e600;
}

.button__content a p span {
  font-size: 1.5rem;
  font-weight: bold;
  color: #5fc1c7;
}

